<template>
    <div class="tag-form tag-tree" v-show="showForm">
        <div v-if="isEdit">
            <el-tree
                ref="tree"
                :data="items"
                :props="default_props"
                :show-checkbox="show_checkbox"
                :default-expand-all="default_expand_all"
                :default-checked-keys="value"
                :filter-node-method="filterNode"
                node-key="id"
                :check-on-click-node="true"
                :expand-on-click-node="false"
                @node-click="_handleNodeClick"
                @check-change="handleCheckChange"
                >
            </el-tree>
            <span v-show="!validateInfo.valid" class="common-error-tip error-info">{{validateInfo.message}}</span>
        </div>
        <span v-else class="view-value">{{value || '--'}}</span>
    </div>
</template>
<script>
import { getAtomFormMixins, getInitialProps } from './atomFormMixins.js'
const TreeAttrs = {
    items: {
        type: Array,
        required: false,
        default () {
            return []
        },
        desc: "set tree data, which should be a multiple layers array"
    },
    show_checkbox: {
        type: Boolean,
        required: false,
        default () {
            return true
        },
        desc: "whether node is selectable"
    },
    default_expand_all: {
        type: Boolean,
        required: false,
        default () {
            return true
        },
        desc: "whether to expand all nodes by default"
    },
    remote: {
        type: Boolean,
        required: false,
        default () {
            return false
        },
        desc: "use remote data or not"
    },
    remote_url: {
        type: String,
        required: false,
        default () {
            return ""
        },
        desc: "remote url when remote is true"
    },
    remote_data_init: {
        type: Function,
        required: false,
        default () {
            return function (data) {
                return data
            }
        },
        desc: "how to process data after getting remote data"
    }
}
export default {
    name: 'TagTree',
    mixins: [getAtomFormMixins(TreeAttrs, this)],
    props: getInitialProps(TreeAttrs),
    data () {
        return {
            default_props: {
                label: 'label',
                children: 'children'
            },
            loading: false,
            remote_cache: null
        }
    },
    mounted () {
        this.remoteMethod(this.remote_url)
    },
    methods: {
        handleCheckChange: function (data, checked, indeterminate) {
            return true
        },
        filterNode: function (value, data) {
            return true
        },
        _handleNodeClick: function (data) {
            if (this.handleNodeClick(data)) {
                this.value = this.$refs.tree.getCheckedKeys(true)
            }
        },
        handleNodeClick: function (data) {
            return true
        },
        remoteMethod (query) {
            var $this = this
            if (!$this.isEdit) return

            var remote_url = typeof this.remote_url === 'function' ? this.remote_url() : this.remote_url
            if (!remote_url) return

            this.loading = true

            // 
            if (this.remote_cache === null && remote_url) {
                $.ajax({
                    url: remote_url,
                    method: "GET",
                    success: function (res) {
                        var data = $this.remote_data_init(res)
                        $this.remote_cache = data

                        if ($this.remote_cache !== null) {
                            $this.items = $this.remote_cache
                            $this.$refs.tree.setCheckedKeys($this.value)
                        } else {
                            $this.items = []
                        }

                        $this.loading = false
                    },
                    error: function () {
                        $this.empty_text = gettext("")
                        $this.loading = false
                    }
                })
            } else {
                $this.items = $this.remote_cache
                $this.$refs.tree.setCheckedKeys($this.value)
                $this.loading = false
            }
        }
    }
}
</script>
