/**
 * vue
 * @param {Object} error 
 * @param {Object} instance
 */
export function errorHandler (error, instance) {
    const data = error.data
    console.log(error)
    if (data) {
        if (!data.code || data.code === 404) {
            instance.exception = {
                code: '404',
                msg: gettext('')
            }
        } else if (data.code === 403) {
            instance.exception = {
                code: '403',
                msg: gettext('sorry!')
            }
        } else if (data.code === 405) {
            instance.exception = {
                code: '405',
                msg: gettext('Sorry!')
            }
        } else if (data.code === 500) {
            instance.exception = {
                code: '500',
                msg: gettext(', , !')
            }
        } else if (data.code === 502) {
            instance.exception = {
                code: '502',
                msg: gettext(', , !')
            }
        } else {
            console.error(error)
            instance.bkMessageInstance = instance.$bkMessage({
                theme: 'error',
                message: error.message || error.data.msg
            })
        }
    } else {
        console.error(error)
        instance.bkMessageInstance = instance.$bkMessage({
            theme: 'error',
            message: error.message || error.data.msg
        })
    }
}
