    position: relative;
    height: 100%;
    overflow: hidden;
    .tool-wrapper {
        position: absolute;
        bottom: 0;
        left: 10px;
        width: 40px;
        color: $black;
        text-align: center;
        z-index: 4;
        .reset-position {
            margin: 0 auto;
            width: 34px;
            height: 34px;
            line-height: 34px;
            font-size: 18px;
            background: $whiteDefault;
            border: 1px solid #d6d6d6;
            border-radius: 2px;
            box-shadow: 0 0 2px rgba(0, 0, 0, 0.15);
            cursor: pointer;
            &:hover {
                color: $blueDefault;
            }
        }
        .zoom-area {
            margin: 10px auto;
            width: 34px;
            background: $whiteDefault;
            border: 1px solid #d6d6d6;
            border-radius: 2px;
            box-shadow: 0 0 2px rgba(0, 0, 0, 0.15);
            .zoom-in, .zoom-out {
                padding: 10px 0;
                font-size: 20px;
                cursor: pointer;
                &:hover {
                    color: $blueDefault;
                }
            }
            .zoom-out {
                padding-top: 0;
                &:hover {
                    .minus {
                        border-color: $blueDefault;
                    }
                }
            }
            .minus {
                display: inline-block;
                width: 8px;
                height: 0;
                border-bottom: 1px solid $black;
            }
        }
    }
}
</style>


