<template>
    <div class="tag-form tag-select" v-show="showForm">
        <div v-if="isEdit">
            <el-select
                v-model="value"
                v-if="isEdit"
                v-loading="loading"
                clearable
                filterable
                :remote="remote"
                :remote-method="remoteMethod"
                :multiple-limit="multiple_limit"
                :multiple="multiple"
                :no-data-text="empty_text"
                :placeholder="placeholder"
                @change="onChange"
                @focus="onFocus">
                <el-option
                    v-for="item in items"
                    v-loading="loading"
                    :key="item.text"
                    :label="item.text"
                    :value="item.value">
                </el-option>
            </el-select>
            <span v-show="!validateInfo.valid" class="common-error-tip error-info">{{validateInfo.message}}</span>
        </div>
        <span v-else class="view-value">{{value || '--'}}</span>
    </div>
</template>
<script>
import { getAtomFormMixins, getInitialProps } from './atomFormMixins.js'
const selectAttrs = {
    items: {
        type: Array,
        required: false,
        default () {
            return []
        },
        desc: "array like [{text: '', value: ''}, {text: '', value: ''}]"
    },
    multiple: {
        type: Boolean,
        required: false,
        default () {
            return false
        },
        desc: "set multiple selected items"
    },
    multiple_limit: {
        type: Number,
        required: false,
        default () {
            return 0
        },
        desc: "limit of selected items when multiple is true"
    },
    remote: {
        type: Boolean,
        required: false,
        default () {
            return false
        },
        desc: "use remote data or not"
    },
    remote_url: {
        type: String,
        required: false,
        default () {
            return ""
        },
        desc: "remote url when remote is true"
    },
    remote_data_init: {
        type: Function,
        required: false,
        default () {
            return function (data) {
                return data
            }
        },
        desc: "how to process data after getting remote data"
    },
    placeholder: {
        type: String,
        required: false,
        default () {
            return ""
        },
        desc: "placeholder"
    },
    empty_text: {
        type: String,
        required: false,
        default () {
            return gettext("")
        },
        desc: "tips when data is empty"
    }
}
export default {
    name: 'TagSelect',
    mixins: [getAtomFormMixins(selectAttrs, this)],
    props: getInitialProps(selectAttrs),
    data () {
        return {
            loading: false,
            remote_cache: null,
            loading_text: gettext("")
        }
    },
    mounted () {
        this.remoteMethod()
    },
    methods: {
        _set_value (value) {
            if (this.remote && this.remote_cache === null) {
                this.remoteMethod("")
            }
            this.value = value
        },
        _get_value () {
            return this.value
        },
        onFocus () {
            if (this.remote && this.remote_cache === null) {
                this.remoteMethod("")
            }
        },
        remoteMethod (query) {
            var $this = this
            var remote_url = typeof this.remote_url === 'function' ? this.remote_url() : this.remote_url
            if (!remote_url) return

            this.loading = true

            // 
            if (this.remote_cache === null && remote_url) {
                $.ajax({
                    url: remote_url,
                    method: "GET",
                    success: function (res) {
                        var data = $this.remote_data_init(res)
                        $this.remote_cache = data

                        if ($this.remote_cache !== null) {
                            if (query) {
                                $this.items = $this.remote_cache.filter(item => {
                                    return item.text.toLowerCase()
                                        .indexOf(query.toLowerCase()) > -1
                                })
                            } else {
                                $this.items = $this.remote_cache
                            }
                        } else {
                            $this.items = []
                        }

                        $this.loading = false
                    },
                    error: function () {
                        $this.placeholder = gettext("")
                        $this.loading = false
                    }
                })
            } else {
                if (query) {
                    $this.items = $this.remote_cache.filter(item => {
                        return item.text.toLowerCase()
                            .indexOf(query.toLowerCase()) > -1
                    })
                } else {
                    $this.items = $this.remote_cache
                }
                $this.loading = false
            }
        }
    }
}
</script>
<style lang="scss" scoped>
    .el-select {
        width: 100%;
    }
</style>
