    saveTemplate (data) {
        const { biz_pk } = store.state
        const { name, template_id, pipeline_tree, category, notify_receivers, notify_type, timeout } = data
        const prefixUrl = this.getPrefix('template')
        const business = this.getPrefix('business') + biz_pk + '/'
        const opts = {
            method: 'POST',
            url: prefixUrl,
            data: {
                name,
                pipeline_tree,
                business: business,
                category,
                notify_receivers,
                notify_type,
                timeout
            }
        }
        if (template_id != undefined) {
            opts.url = `${prefixUrl}${template_id}/`
            opts.headers = {'X-HTTP-Method-Override': 'PATCH'}
        }
        return request(opts)
    },
