<template>
    <bk-dialog
    :is-show="isModalShow"
    :title="` `"
    :width="'600'"
    padding="45px"
    :has-header="true"
    :has-footer="false"
    :quick-close="false"
    :close-icon="true"
    @cancel="onCloseDialog">
        <div slot="content" class="error-content">
            <div class="pic-wrapper">
                <img :src="errorPic" class="error-pic" alt="error-pic">
            </div>
            <ErrorCode401 v-if="code==='401'"></ErrorCode401>
            <ErrorCode403 v-if="code==='403'"></ErrorCode403>
            <ErrorCode405 v-if="code==='405'"></ErrorCode405>
            <ErrorCode500 v-if="code==='500'"></ErrorCode500>
        </div>
    </bk-dialog>
</template>
<script>
import ErrorCode401 from './ErrorCode401.vue'
import ErrorCode403 from './ErrorCode403.vue'
import ErrorCode405 from './ErrorCode405.vue'
import ErrorCode500 from './ErrorCode500.vue'
export default {
    name: 'ErrorCodeModal',
    components: {
        ErrorCode403,
        ErrorCode401,
        ErrorCode405,
        ErrorCode500
    },
    data () {
        return {
            isModalShow: false,
            code: '',
            expPic401: require('@/assets/images/expre_401.png'),
            expPic403: require('@/assets/images/expre_403.png'),
            expPic500: require('@/assets/images/expre_500.png')
        }
    },
    computed: {
        errorPic () {
            if (this.code === '500') {
                return this.expPic500
            } else if (this.code === '401') {
                return this.expPic401
            }
            return this.expPic403
        }
    },
    methods: {
        show (code) {
            this.code = code
            this.isModalShow = true
        },
        onCloseDialog () {
            this.isModalShow = false
        }
    }
}
</script>
<style lang="scss" scoped>
.error-content {
    padding-bottom: 40px;
    /deep/ .error-title {
        margin: 10px 0;
        font-weight: bold;
    }
    .pic-wrapper {
        text-align: center;
        .error-pic {
            width: 360px;
            height: 168px;
        }
    }
    
}
</style>

