<template>
<div id="app">
    <navigator v-if="!hideHeader"/>
    <UserLoginModal ref="userLogin"></UserLoginModal>
    <ErrorCodeModal ref="errorModal"></ErrorCodeModal>
    <router-view></router-view>
</div>
</template>
<script>
import Vue from 'vue'
import { mapState, mapMutations, mapActions } from 'vuex'
import bus from '@/utils/bus.js'
import UserLoginModal from '@/components/common/modal/UserLoginModal.vue'
import ErrorCodeModal from '@/components/common/modal/ErrorCodeModal.vue'
import Navigator from '@/components/layout/Navigator.vue'

export default {
    name: 'App',
    components: {
        Navigator,
        UserLoginModal,
        ErrorCodeModal
    },
    computed: {
        ...mapState({
            'hideHeader': state => state.hideHeader,
            'viewMode': state => state.view_mode
        })
    },
    created () {
        this.getBizPK()
        if (this.viewMode === 'appmaker') {
            this.setTemplateId(this.$route.query.template_id)
        }
    },
    mounted () {
        bus.$on('showLoginModal', (src) => {
            this.$refs.userLogin.show(src)
        })
        bus.$on('showErrorModal', (type) => {
            this.$refs.errorModal.show(type)
        })
        bus.$on('showMessage', (info) => {
            this.$bkMessage({
                message: info.message,
                theme: info.theme || 'error'
            })
        })

    },
    methods: {
        ...mapActions([
            'getBizPK'
        ]),
        ...mapMutations([
            'setTemplateId'
        ])
    }
}
</script>
<style lang="scss">
    @import './scss/app.scss';
    html,body {
        height:100%;
    }
    #app {
        height: 100%;
    }
</style>
