<template>
    <div class="modify-params-container">
        <div class="panel-title">
            <h3>{{ i18n.change_params }}</h3>
        </div>
        <div class="edit-wrapper">
            <TaskParamEdit
                v-if="isEmptyParams"
                ref="TaskParamEdit"
                :constants="pipelineData.constants">
            </TaskParamEdit>
            <NoData v-else></NoData>
        </div>
        <div class="action-wrapper" v-if="isEmptyParams">
            <bk-button type="success" @click="onModifyParams">{{ i18n.save }}</bk-button>
        </div>
    </div>
</template>
<script>
import {mapActions} from 'vuex'
import { errorHandler } from '@/utils/errorHandler.js'
import NoData from '@/components/common/base/NoData.vue'
import TaskParamEdit from '../TaskParamEdit.vue'
export default {
    name: 'ModifyParams',
    components: {
        TaskParamEdit,
        NoData
    },
    props: ['pipelineData', 'instance_id'],
    data () {
        return {
            i18n: {
                change_params: gettext(""),
                save: gettext("")
            },
            bkMessageInstance: null
        }
    },
    computed: {
        isEmptyParams () {
            return Object.keys(this.pipelineData.constants).length
        }
    },
    methods: {
        ...mapActions('task/', [
            'instanceModifyParams'
        ]),
        async onModifyParams () {
            const paramEditComp = this.$refs.TaskParamEdit
            const formData = {}
            let formValid = true
            if (paramEditComp) {
                formValid = paramEditComp.validate()
                if (!formValid) return
                const variables = paramEditComp.getVariableData()
                for (let key in variables) {
                    formData[key] = variables[key].value
                }
            }

            const data = {
                instance_id: this.instance_id,
                constants: JSON.stringify(formData)
            }
            try {
                const res = await this.instanceModifyParams(data)
                if (res.result) {
                    this.$bkMessage({
                        message: gettext(''),
                        theme: 'success'
                    })
                } else {
                    errorHandler(res, this)
                }
            } catch (e) {
                errorHandler(e, this)
            }
        }
    }
}
</script>
<style lang="scss" scoped>
    @import '@/scss/config.scss';
    @import '@/scss/mixins/scrollbar.scss';
    .modify-params-container {
        position: relative;
        height: 100%;
        overflow: hidden;
        .panel-title {
            padding: 20px;
            h3 {
                margin: 0;
                font-size: 22px;
                font-weight: normal;
            }
        }
        .edit-wrapper {
            padding: 20px 20px 0;
            height: calc(100% - 150px);
            overflow-y: auto;
            @include scrollbar;
        }
        .action-wrapper {
            height: 90px;
            line-height: 90px;
            text-align: center;
            border-top: 1px solid $commonBorderColor;
        }
    }
</style>


