import Vue from 'vue'
import Vuex from 'vuex'
import modules from './modules/index.js'
import api from "@/api"

Vue.use(Vuex)

function getUserType () {
    let userType = ''
    if (window.IS_FUNCTOR === 1) {
        userType = 'functor'
    } else if (window.IS_AUDITOR === 1) {
        userType = 'auditor'
    } else {
        userType = 'maintainer'
    }
    return userType
}

const store = new Vuex.Store({
    strict: process.env.NODE_ENV !== 'production',
    state: {
        username: window.USERNAME,
        userType: getUserType(),
        hideHeader: window.HIDE_HEADER === 1,
        site_url: window.SITE_URL,
        biz_pk: '',
        app_id: window.APP_ID,
        view_mode: window.VIEW_MODE,
        cc_id: window.BIZ_CC_ID,
        run_ver: window.RUN_VER,
        bizList: [],
        templateId: '',
        notFoundPage: false,
        firstEnterApp: true  // hack spa page goto Django page
    },
    mutations: {
        setBizPK (state, id) {
            state.biz_pk = id
        },
        setAppId (state, id) {
            state.app_id = id
        },
        setViewMode (state, mode) {
            state.view_mode = mode
        },
        setBizId (state, id) {
            state.cc_id = id
        },
        setBizList (state, data) {
            state.bizList = data
        },
        setTemplateId (state, id) {
            state.templateId = id
        },
        setNotFoundPage (state, val) {
            state.notFoundPage = val
        },
        markFirstEnter (state) {
            state.firstEnterApp = false
        }
    },
    actions: {
        getBizPK ({commit, state}) {
            api.getBizPK(state.cc_id).then(response => {
                commit('setBizPK', response.data.objects[0].id)
            })
        },
        getBizList ({commit}) {
            api.getBizList().then(response => {
                commit('setBizList', response.data.data)
            })
        }
    },
    getters: {},
    modules
})

export default store
