<template>
    <div class="template-container">
        <div class="list-wrapper">
            <h3 class="wrapper-header">{{ i18n.flows }}</h3>
            <div class="template-table-content">
                <div class="operation-area clearfix">
                    <router-link class="bk-button bk-primary create-template" :to="`/template/new/${cc_id}`" tag="a">{{ i18n.new }}</router-link>
                </div>
                <table v-bkloading="{isLoading: loading, opacity: 1}">
                    <thead>
                        <tr>
                            <th class="template-name">{{ i18n.name }}</th>
                            <th>{{ i18n.type }}</th>
                            <th>{{ i18n.update_time }}</th>
                            <th>{{ i18n.creator }}</th>
                            <th>{{ i18n.operation }}</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in templateList" :key="item.id">
                            <td class="template-name">
                                <router-link :to="`/template/edit/${cc_id}/?template_id=${item.id}`">{{item.name}}</router-link>
                            </td>
                            <td>{{item.category_name}}</td>
                            <td>{{item.edit_time}}</td>
                            <td>{{item.creator_name}}</td>
                            <td>
                                <router-link class="bk-button bk-success" :to="`/template/newtask/${cc_id}/selectnode/?template_id=${item.id}`">{{ i18n.new_task }}</router-link>
                                <router-link class="bk-button bk-warning" :to="`/template/edit/${cc_id}/?template_id=${item.id}`">{{ i18n.edit }}</router-link>
                                <router-link class="bk-button bk-default" :to="`/template/clone/${cc_id}/?template_id=${item.id}`">{{ i18n.clone }}</router-link>
                                <bk-button type="danger" @click="onDeleteTemplate(item.id)">{{ i18n.delete }}</bk-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
                <div class="panagation">
                    <bk-paging
                        :cur-page.sync="currentPage"
                        :total-page="totalPage">
                    </bk-paging>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import { errorHandler } from '@/utils/errorHandler.js'
export default {
    name: 'TemplateList',
    components: {},
    props: ['cc_id'],
    data () {
        return {
            i18n: {
                flows: gettext(""),
                new: gettext(""),
                name: gettext(""),
                type: gettext(""),
                update_time: gettext(""),
                creator: gettext(""),
                operation: gettext(""),
                new_task: gettext(""),
                edit: gettext(""),
                clone: gettext(""),
                delete: gettext("")
            },
            loading: true,
            currentPage: 1,
            totalPage: 1
        }
    },
    computed: {
        ...mapState({
            templateList: state => state.templateList.templateListData
        })
    },
    watch: {},
    mounted () {
        this.getTemplateList()
    },
    updated () {},
    destroyed () {},
    methods: {
        ...mapActions('templateList/', [
            'loadTemplateList',
            'deleteTemplate'
        ]),
        ...mapMutations('templateList/', [
            'setTemplateListData'
        ]),
        async getTemplateList () {
            this.loading = true
            try {
                const list = await this.loadTemplateList()
                this.setTemplateListData(list)
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.loading = false
            }
        },
        async onDeleteTemplate (id) {
            try {
                await this.deleteTemplate(id)
                await this.getTemplateList(id)
            } catch (e) {
                errorHandler(e, this)
            }
        }
    }
}
</script>
