<template>
    <header>
        <img :src="logo" class="logo"/>
        <nav>
            <div class="navigator">
                <router-link
                    v-for="route in route_list"
                    :key="route.key"
                    :to="getGotoPath(route)"
                    :class="{
                        'nav-item': true,
                        'active-module': getPathActivedState(route.key)
                    }"
                    tag="a">{{route.name}}</router-link>
                <a target="_blank" href="http://docs.bk.tencent.com/product_white_paper/gcloud/" class="nav-item">{{ i18n.help }}</a>
            </div>
        </nav>
        <div class="header-right" v-if="showHeaderRight">
            <div class="biz-list">
                <bk-dropdown
                    :list="businessList"
                    :selected.sync="currentCcId"
                    @item-selected="onSelectBiz">
                </bk-dropdown>
            </div>
            <div class="user-name">{{username}}</div>
        </div>
    </header>
</template>
<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import { errorHandler } from '@/utils/errorHandler.js'
const ROUTE_LIST = {
    functor_router_list: [
        {
            key: 'function',
            path: '/function/home/',
            name: gettext('')
        }
    ],
    auditor_router_list: [
        {
            key: 'audit',
            path: '/audit/home/',
            name: gettext('')
        }
    ],
    maintainer_router_list: [
        {
            key: 'business',
            path: '/business/home/',
            name: gettext('')
        },
        {
            key: 'template',
            path: '/template/home/',
            name: gettext('')
        },
        {
            key: 'taskflow',
            path: '/taskflow/home/',
            name: gettext('')
        },
        {
            key: 'config',
            path: '/config/home/',
            name: gettext('')
        },
        {
            key: 'appmaker',
            path: '/appmaker/home/',
            name: gettext('')
        }
    ]
}

export default {
    name: 'Navigator',
    data () {
        return {
            i18n: {
                help: gettext("")
            },
            logo: require('../../assets/images/logo/' + gettext('logo-zh') + '.svg')
        }
    },
    computed: {
        ...mapState({
            username: state => state.username,
            userType: state => state.userType,
            cc_id: state => state.cc_id,
            app_id: state => state.app_id,
            view_mode: state => state.view_mode,
            run_ver: state => state.run_ver,
            bizList: state => state.bizList,
            templateId: state => state.templateId,
            notFoundPage: state => state.notFoundPage
        }),
        businessList () {
            return this.bizList.map(item => {
                return {
                    id: item.id,
                    name: item.text
                }
            })
        },
        currentCcId: {
            get () {
                return this.cc_id
            },
            set (id) {
                this.setBizId(id)
            }
        },
        showHeaderRight () {
            if (
                this.userType === 'maintainer' &&
                this.view_mode !== 'appmaker' &&
                this.bizList.length
            ) {
                return true
            }
            return false
        },
        route_list () {
            if (this.view_mode === 'appmaker') {
                return [
                    {
                        key: 'appmakerTaskCreate',
                        path: `/appmaker/${this.app_id}/newtask/${this.cc_id}/selectnode`,
                        query: {template_id: this.template_id},
                        name: gettext('')
                    },
                    {
                        key: 'appmakerTaskList',
                        path: `/appmaker/${this.app_id}/task_home/`,
                        name: gettext('')
                    }
                ]
            } else {
                let routes = ROUTE_LIST[`${this.userType}_router_list`]
                if (this.run_ver === 'community') {
                    routes = routes.filter(item => item.key !== 'appmaker')
                }
                return routes
            }
        }
    },
    mounted () {
        if (this.userType === 'maintainer' && this.view_mode !== 'appmaker') {
            this.getBizList()
        }
    },
    methods: {
        ...mapActions([
            'getBizList',
            'getBizPK'
        ]),
        ...mapMutations([
            'setBizId'
        ]),
        getPathActivedState (key) {
            if (this.view_mode === 'appmaker') {
                return key === 'appmakerTaskCreate'
            }
            if (this.userType === 'functor') {
                return key === 'function'
            } else if (this.userType === 'auditor') {
                return key === 'audit'
            }
            const regx = new RegExp('^\/' + key)
            return regx.test(this.$route.path)
        },
        getGotoPath (route) {
            if (this.notFoundPage) {
                return '/'
            }
            if (route.key === 'appmakerTaskCreate') {
                return `${route.path}?template_id=${this.templateId}`
            } else if (this.userType !== 'maintainer') {
                return `${route.path}`
            } else {
                return `${route.path}${this.cc_id}/`
            }
        },
        async onSelectBiz (id) {
            try {
                await this.getBizPK()
                this.$router.push({path: `/business/home/${id}/`})
            } catch (e) {
                errorHandler(e, this)
            }
        }
    }
}
</script>
