import axios from 'axios'
import bus from '@/utils/bus.js'
import { checkDataType } from '@/utils/checkDataType'
import { setJqueryAjaxConfig } from '@/config/setting.js'

// jquery ajax error handler
setJqueryAjaxConfig()

axios.interceptors.request.use(function (config) {
    return config
}, function (error) {
    return Promise.reject(error)
})

axios.interceptors.response.use(
    response => {
        return response
    },
    error => {
        const response = error.response
        console.log(response)

        switch (response.status) {
            case 400:
                const info = {
                    message: response.data.error || response.data.msg.error,
                    theme: 'error'
                }
                bus.$emit('showMessage', info)
                break
            case 401:
                // bus.$emit('showLoginModal', src)
                bus.$emit('showErrorModal', '401')
                break
            case 403:
                bus.$emit('showErrorModal', '403')
                break
            case 404:
                // window.location.href = SITE_URL + '404'
                break
            case 405:
                bus.$emit('showErrorModal', '405')
                break
            case 500:
                bus.$emit('showErrorModal', '500')
                break
        }
        if (!response.data) {
            let msg = gettext('')
            console.warn(gettext(''), gettext('HTTP'), response.status)
            console.error(msg)
            response.data = {
                code: response.status,
                msg: msg
            }
        } else {
            let msg = response.data
            response.data = {
                code: response.status,
                msg
            }
        }
        if (response.data.message) {
            if (checkDataType(response.data.message) === 'Object') {
                let msg = []
                for (let key in response.data.message) {
                    msg.push(response.data.message[key].join(';'))
                }
                response.data.msg = msg.join(';')
            } else if (checkDataType(response.data.message) === 'Array') {
                response.data.msg = response.data.message.join(';')
            } else {
                response.data.msg = response.data.message
            }
        }
        if (response.data.responseText) {
            response.data.msg = response.data.responseText
        }
        return Promise.reject(response)
    }
)
