<script>
/**
 * 
 *
 * {Object} config 
 * {Object} option  UI  labelcheckboxgroupName
 * {Object} data 
 */
import { random4 } from '@/utils/uuid.js'
import { checkDataType } from '@/utils/checkDataType.js'
import TagGroup from './TagGroup.vue'
import TagRadio from './TagRadio.vue'
import Tagcheckbox from './TagCheckbox.vue'
import TagInput from './TagInput.vue'
import TagInt from './TagInt.vue'
import TagSelect from './TagSelect.vue'
import TagTextarea from './TagTextarea.vue'
import TagDatetime from './TagDatetime.vue'
import TagDatatable from './TagDatatable.vue'
import TagUpload from './TagUpload.vue'
import TagTree from './TagTree.vue'

import BaseCheckbox from '../base/BaseCheckbox.vue'
import BaseInput from '../base/BaseInput.vue'

const FORM_ATTRS = [
    'items', 'placeholder', 'multiple', 'validation', 'editable', 'empty_text',
    'columns', 'add_btn', 'remote', 'remote_url', 'remote_data', 'remote_data_init',
    'url', 'headers', 'limit', 'auto_upload',
    'show_checkbox', 'default_expand_all'
]

export default {
    name: 'RenderForm',
    components: {
        'tag-group': TagGroup,
        'tag-radio': TagRadio,
        'tag-checkbox': Tagcheckbox,
        'tag-input': TagInput,
        'tag-int': TagInt,
        'tag-select': TagSelect,
        'tag-textarea': TagTextarea,
        'tag-datetime': TagDatetime,
        'tag-datatable': TagDatatable,
        'tag-upload': TagUpload,
        'tag-tree': TagTree,
        BaseCheckbox,
        BaseInput
    },
    props: {
        config: {
            type: Array,
            default () {
                return []
            }
        },
        option: {
            type: Object,
            default () {
                return {
                    showHook: false,
                    showGroup: false,
                    showLabel: false,
                    isEdit: true
                }
            }
        },
        data: {
            type: Object
        }
    },
    methods: {
        validate () {
            let isValid = true
            for (let ref in this.$refs) {
                let singleItemValid = true
                const component = this.$refs[ref]
                if (checkDataType(component) === 'Undefined') {
                    delete this.$refs[ref]
                } else if (checkDataType(component) === 'Array') {
                    component.forEach(item => {
                        singleItemValid = !item.validate || item.validate()
                    })
                } else {
                    singleItemValid = !component.validate || component.validate()
                }
                if (isValid) {
                    isValid = singleItemValid
                }
            }
            return isValid
        }
    },
    render (h) {
        const self = this
        function getFormItem (config, option, data, h) {
            return  config.map((item, formIndex) => {
                const formId = item.tag_code + '_' + formIndex
                const isRenderTagHook = item.attrs.hookable && option.showHook
                let isHooked
                if (item.variableKey) {
                    isHooked = data.hook && (data.hook[item.variableKey] || data.hook[item.tag_code])
                } else {
                    isHooked = data.hook && (data.hook[item.tag_code] || data.hook['${' + item.tag_code + '}'])
                }
                // combine
                if (item.type === 'combine' && item.attrs.children.length) {
                    const childrenItems = []
                    const childrenOption = Object.assign({}, option)
                    if (option.showGroup) {  // 
                        const groupName =  data.extend && data.extend[item.variableKey].name
                        const groupLabel = h('div', {class: 'group-name'},
                            [
                                h('h3', {class: 'name'}, groupName)
                            ]
                        )
                        childrenOption.showGroup = false
                        childrenItems.push(groupLabel)
                    }
                    if (isRenderTagHook) { // hook
                        childrenItems.push(h('BaseCheckbox', {
                            class: 'tag-hook',
                            props: {
                                isChecked: isHooked
                            },
                            on: {
                                'checkCallback' (checked) {
                                    self.$emit('hookChange', checked, item.tag_code, item.variableKey)
                                }
                            }
                        }))
                    }
                    //  form-item 
                    item.attrs.children.forEach(cItem => {
                        cItem.variableKey = item.variableKey
                    })
                    let groupData = data
                    if (item.variableKey) { // 
                        groupData = {
                            hook: data.hook[item.variableKey],
                            value: data.value[item.variableKey]
                        }
                    }
                    if (isHooked) {
                        childrenItems.push(
                            h('div', {
                                class: "form-item clearfix"
                            }, [
                                h('label', {
                                    class: {
                                        'tag-label': true,
                                        'required': false
                                    }
                                }, item.attrs.name),
                                h('div',
                                    {
                                        class: {
                                            'tag-form': true
                                        }
                                    },
                                    [h(`BaseInput`, {
                                        class: {
                                            'baseInput': true,
                                            'disabled': true
                                        },
                                        attrs: {
                                            disabled: true,
                                            value: groupData.value
                                        }
                                    })]
                                )
                            ])
                        )
                    } else {
                        childrenItems.push(getFormItem(item.attrs.children, childrenOption, groupData, h))
                    }
                    return h(`tag-group`, {
                        'class': ['form-group']
                    }, childrenItems)
                }

                const formComponents = []
                const propAttrs = {}
                FORM_ATTRS.forEach(attr => {
                    let attrKey = `initial${attr.charAt(0).toUpperCase() + attr.slice(1)}`
                    if (item.attrs[attr]) {
                        propAttrs[attrKey] = item.attrs[attr]
                    }
                })
                const hasAttrsInConfig = !!item.attrs
                let propValue = ''
                let valueFromUp
                if (checkDataType(data.value) === 'Object') {
                    if (item.variableKey in data.value) {
                        valueFromUp = data.value[item.variableKey]
                    } else if (item.tag_code in data.value) {
                        valueFromUp = data.value[item.tag_code]
                    } else {
                        data.value['${' + item.tag_code + '}']
                    }
                } else {
                    valueFromUp = data.value
                }
                if (valueFromUp && valueFromUp != undefined) {
                    if (typeof valueFromUp === 'string') {
                        propValue = valueFromUp
                    } else if (Array.isArray(valueFromUp)){
                        propValue = [...valueFromUp]
                    } else {
                        propValue = valueFromUp || data.value['${' + item.tag_code + '}']
                    }
                } else if (hasAttrsInConfig && item.attrs.default) {
                    propValue = item.attrs.default
                } else if ((hasAttrsInConfig && item.attrs.multiple) || item.type === 'upload' || item.type === 'datatable' || item.type === 'tree' || item.type === 'checkbox') {
                    propValue = []
                }
                // name
                if (option.showGroup) {
                    const name = data.extend && data.extend[item.variableKey] && data.extend[item.variableKey].name
                    formComponents.push(h('div', {class: {'group-name': true}},
                        [
                            h('h3', {class: {'name': true}}, name || item.attrs.name)
                        ]
                    ))
                }
                // desc
                if (option.showDesc && data.extend && data.extend[item.variableKey] && data.extend[item.variableKey].desc) {
                    formComponents.push(
                        h('bk-tooltip', {
                            class: {'desc': true},
                            props: {
                                content: data.extend[item.variableKey].desc,
                                placement: 'left'
                            }
                        }, [
                            h('i', {
                                class: {
                                    'common-icon-warning': true
                                }
                            })
                        ])
                    )
                }
                // label
                if (option.showLabel) {
                    let labelName = ''
                    let isRequired = false
                    if (item.attrs) {
                        labelName = item.attrs.name
                        if (item.attrs.validation) {
                            isRequired = item.attrs.validation.some(item => {
                                return item.type === 'required'
                            })
                        }
                    }
                    formComponents.push(
                        h('label', {
                            class: {
                                'tag-label': true,
                                'required': isRequired
                            }
                        }, labelName)
                    )
                }
                if (isHooked) {
                    // checked input
                    formComponents.push(
                        h('div',
                            {
                                class: {
                                    'tag-form': true
                                }
                            },
                            [h(`BaseInput`, {
                                class: {
                                    'baseInput': true,
                                    'disabled': true
                                },
                                attrs: {
                                    disabled: true,
                                    value: propValue
                                },
                                ref: formId
                            })]
                        )
                    )
                } else {
                // full form
                    const isEdit = ('isEdit' in option) ? option.isEdit : true
                    formComponents.push(
                        h(`tag-${item.type}`, {
                            on: {
                                change: function (val, tagCode) {
                                    self.$emit('dataChange', val, tagCode, item.variableKey)
                                }
                            },
                            props: {
                                initialTag_id: formId,
                                initialTag_code: item.tag_code,
                                initialType: item.type,
                                initialValue: propValue,
                                initialMethods: item.methods || {},
                                initialEvents: item.events || [],
                                initialIsEdit: isEdit,
                                ...propAttrs
                            },
                            key: item.tag_code,
                            ref: formId
                        })
                    )
                }
                // hook
                if (isRenderTagHook) {
                    formComponents.push(
                        h('bk-tooltip', {
                            class: 'tag-hook',
                            props: {
                                content: isHooked ? gettext('') : gettext(''),
                                placement: 'left'
                            }
                        }, [
                            h('BaseCheckbox', {
                                props: {
                                    isChecked: isHooked
                                },
                                on: {
                                    'checkCallback' (checked) {
                                        self.$emit('hookChange', checked, item.tag_code, item.variableKey)
                                    }
                                }
                            })
                        ])
                    )
                }
                return h('div', {
                    class: "form-item clearfix"
                }, formComponents)
            })
        }
        const formItems = getFormItem(this.config, this.option, this.data, h)
        return h('div', {class: "render-form"}, formItems)
    }
}
</script>
<style lang="scss" scoped>
    .group-name {
        display: block
    }
    .form-group {
        position: relative;
    }
    .form-item {
        position: relative;
        margin: 15px 0;
        &:first-child {
            margin-top: 0;
        }
        /deep/ .view-value {
            display: inline-block;
            height: 36px;
            line-height: 36px;
            font-size: 14px;
            word-wrap: break-word;
            word-break: break-all;
        }
    }
    .tag-label {
        float: left;
        position: relative;
        margin-top: 8px;
        width: 100px;
        font-size: 14px;
        font-weight: bold;
        color: #666666;
        text-align: right;
        word-wrap: break-word;
        word-break: break-all;
        &.required {
            &:before {
                content: '*';
                position: absolute;
                top: 0px;
                right: -10px;
                color: #F00;
                font-family: "SimSun";
            }
        }
    }
    .tag-form {
        margin-left: 120px;
    }
    .tag-hook {
        position: absolute;
        top: 11px;
        right: 0;
        z-index: 1;
    }
</style>
