import api from "@/api"

const templateList = {
    namespaced: true,
    state: {
        templateListData: []
    },
    mutations: {
        setTemplateListData (state, data) {
            state.templateListData = data
        }
    },
    actions: {
        loadTemplateList ({commit}) {
            return api.getTemplateList().then(response => response.data.objects)
        },
        deleteTemplate ({commit}, template_id) {
            return api.deleteTemplate(template_id).then(response => response.data.objects)
        }
    },
    getters: {}
}

export default templateList