const tools = {
    debounce (fn, delay) {
        let timer
      
        return function () {
            let context = this
            let args = arguments
        
            clearTimeout(timer)
        
            timer = setTimeout(function () {
                fn.apply(context, args)
            }, delay)
        }
    },
    throttle (fn, threshhold) {
        let last, timer

        threshhold || (threshhold = 250)

        return function () {
            var context = this
            var args = arguments
            var now = +new Date()

            if (last && now < last + threshhold) {
                clearTimeout(timer)

                timer = setTimeout(function () {
                    last = now
                    fn.apply(context, args)
                }, threshhold)

            } else {
                last = now
                fn.apply(context, args)
            }
        }
    },
    deepClone (obj) {
        if (typeof obj !== 'object') return
        var newObj = obj instanceof Array ? [] : {}
        for (var key in obj) {
            if (obj.hasOwnProperty(key)) {
                newObj[key] = typeof obj[key] === 'object' ? this.deepClone(obj[key]) : obj[key]
            }
        }
        return newObj
    },
    isObjEqual (a, b) {
        let p, t
        for (p in a) {
            if (typeof b[p] === 'undefined') {
                return false
            }
            if (b[p] && !a[p]) {
                return false
            }
            t = typeof a[p]
            if (t === 'object' && !this.isObjEqual(a[p], b[p])) {
                return false
            }
            if (t === 'function' && (typeof b[p] === 'undefined' || a[p].toString() !== b[p].toString())) {
                return false
            }
            if (a[p] !== b[p]) {
                return false
            }
        }
        for (p in b) {
            if (typeof a[p] === 'undefined') {
                return false
            }
        }
        return true
    }
}

export default tools