<template>
    <span
        :class="['base-checkbox', {checked: this.checked}]"
        @click.stop="onCheck">
        <i class="common-icon-done-thin" v-show="this.checked"></i>
    </span>
</template>
<script>
export default {
    name: 'BaseCheckbox',
    props: ['isChecked'],
    data () {
        return {
            checked: this.isChecked || false
        }
    },
    methods: {
        onCheck () {
            this.checked = !this.checked
            this.$emit('checkCallback', this.checked)
        }
    },
    watch: {
        isChecked (val) {
            this.checked = val
        }
    }
}
</script>
<style lang="scss" scoped>
@import '../../../scss/config.scss';
.base-checkbox {
    display: inline-block;
    width: 14px;
    height: 14px;
    line-height: 14px;
    color: $whiteDefault;
    font-size: 12px;
    border: 1px solid $formBorderColor;
    border-radius: 2px;
    text-align: center;
    cursor: pointer;
    &:hover {
        border-color: $greyDark;
    }
    &.checked {
        background: $blueDefault;
        border-color: $blueDefault;
    }
}
</style>
