<template>
    <div class="task-container">
        <div class="list-wrapper">
            <h3 class="wrapper-header">{{ i18n.task_list }}</h3>
            <div class="task-table-content">
                <div class="operation-area clearfix">
                </div>
                <table v-bkloading="{isLoading: loading, opacity: 1}">
                    <thead>
                        <tr>
                            <th class="task-name">{{ i18n.task_name }}</th>
                            <th>{{ i18n.start_time }}</th>
                            <th>{{ i18n.finish_time }}</th>
                            <th>{{ i18n.task_type }}</th>
                            <th>{{ i18n.creator }}</th>
                            <th>{{ i18n.executor }}</th>
                            <th>{{ i18n.status }}</th>
                            <th>{{ i18n.operation }}</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in taskList" :key="item.id">
                            <td class="task-name">
                                <router-link :to="`/taskflow/execute/2/?instance_id=${item.id}`">{{item.name}}</router-link>
                            </td>
                            <td>{{item.start_time || '-'}}</td>
                            <td>{{item.finish_time || '-'}}</td>
                            <td>{{item.category_name}}</td>
                            <td>{{item.creator_name}}</td>
                            <td>{{item.executor_name || '-'}}</td>
                            <td>{{item.current_flow}}</td>
                            <td class="operation-wrapper">
                                <bk-button type="success" @click="onCloneTask(item.id)">{{ i18n.clone }}</bk-button>
                                <bk-button type="danger" @click="onDeleteTask(item.id)">{{ i18n.delete }}</bk-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
                <div class="panagation">
                    <bk-paging
                        :cur-page.sync="currentPage"
                        :total-page="totalPage">
                    </bk-paging>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import { errorHandler } from '@/utils/errorHandler.js'

export default {
    name: 'TaskList',
    components: {},
    props: ['cc_id'],
    data () {
        return {
            i18n: {
                task_list: gettext(""),
                task_name: gettext(""),
                start_time: gettext(""),
                finish_time: gettext(""),
                task_type: gettext(""),
                creator: gettext(""),
                executor: gettext(""),
                status: gettext(""),
                operation: gettext(""),
                clone: gettext(""),
                delete: gettext("")
            },
            loading: true,
            currentPage: 1,
            totalPage: 1
        }
    },
    computed: {
        ...mapState({
            taskList: state => state.taskList.taskListData
        })
    },
    watch: {},
    mounted () {
        this.getTaskList()
    },
    updated () {},
    destroyed () {},
    methods: {
        ...mapActions('taskList/', [
            'loadTaskList',
            'deleteTask',
            'cloneTask'
        ]),
        ...mapMutations('taskList/', [
            'setTaskListData'
        ]),
        async getTaskList () {
            this.loading = true
            try {
                const list = await this.loadTaskList()
                this.setTaskListData(list)
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.loading = false
            }
        },
        async onDeleteTask (id) {
            try {
                await this.deleteTask(id)
                await this.getTaskList()
            } catch (e) {
                errorHandler(e, this)
            }
        },
        async onCloneTask (id) {
            try {
                const data = await this.cloneTask(id)
                this.$router.push({path: `/taskflow/execute/${this.cc_id}/`, query: {instance_id: data.data.new_instance_id}})
            } catch (e) {
                errorHandler(e, this)
            }
        }
    }
}
</script>
